@mixin before($txt:'') {
  &:before {
    content: $txt;
    display: block;
    @content; } }

@mixin after($txt:'') {
  &:after {
    content: $txt;
    display: block;
    @content; } }

@mixin hover($w:768px) {
  @media only screen and (min-width: $w) {
    &:hover {
      @content; } } }

@mixin pstc0($t:0,$l:0,$b:0,$r:0) {
  position: absolute;
  top: $t;
  bottom: $b;
  left: $l;
  right: $r;
  margin: auto; }

@mixin pstc5($t:50%,$l:50%) {
  position: absolute;
  top: $t;
  left: $l;
  transform: translate(-50%,-50%); }

@mixin dib {
  display: inline-block;
  vertical-align: middle; }
