@import "./_base";
@import "./_variable";

@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Noto+Sans+TC:wght@400;700&display=swap');

* {
  padding: 0;
  margin: 0;
  font-family: 'Noto Sans TC', sans-serif; }

img {
  width: 100%;
  vertical-align: top; }

header {
  width: 100%;
  padding: 15px 30px;
  box-sizing: border-box;
  figure.logo {
    width: 145px; } }

.num {
  font-family: 'Lato','Noto Sans TC', sans-serif; }

html {
  font-size: 20px;
  article {
    section.section01 {
      background-color: $yellow;
      .wrapper {
        max-width: 1580px;
        width: 100%;
        padding: 30px 15px;
        margin: auto;
        box-sizing: border-box;
        figure.title {
          max-width: 1050px;
          margin: auto; }
        .box {
          padding: 15px 0;
          .owl-item {
            &:nth-child(even) {
              padding-top: 60px; } }
          .owl-dots {
            text-align: center;
            .owl-dot {
              width: 20px;
              height: 20px;
              margin: 0 5px;
              border-radius: 50%;
              background-color: #fff;
              &.active {
                background-color: $orange; } } } } }
      @include rwd(768px) {
        .wrapper {
          padding: 30px;
          figure.title {
            // max-width: 1050px
            width: 90%; }
          .box {
            // padding: 15px 0
            .owl-item {
              &:nth-child(even) {
                padding-top: 0px; } } } } } }
    section.section02 {
      width: 100%;
      padding: 80px 0;
      background-image: url(../images/bg01.png);
      background-size: 34px;
      background-position: -10px -10px;
      .wrapper {} }
    section.section03 {
      background-color: $pink;
      .wrapper {
        width: 100%;
        margin: auto;
        figure.title {
          width: 630px;
          margin: auto;
          position: relative;
          top: -40px; }
        .container {
          padding: 60px 0;
          text-align: center;
          .circle {
            @include dib;
            position: relative;
            @include before {
              width: 100%;
              padding-bottom: 100%;
              background-color: #ffa1a1;
              border: 2px solid #db7777;
              border-radius: 50%;
              @include pstc5;
              z-index: 0; }
            .info {
              padding: 0 50px;
              position: relative;
              z-index: 1;
              .txt1 {
                width: 110px;
                margin: 0 auto 10px;
                font-size: 1.1rem;
                line-height: 1.8rem;
                color: #db7777;
                background-color: #fff;
                border-radius: 0.9rem; }
              .txt2 {
                font-size: 2rem;
                line-height: 1.3;
                color: #706061; }
              .txt3 {
                font-size: 2rem;
                line-height: 1.3;
                color: #706061;
                font-weight: bold; } } }
          figure.arrow {
            width: 28px;
            margin: 0 25px;
            @include dib; }
          .box {
            @include dib;
            .item {
              padding: 0 60px 0 120px;
              margin-bottom: 60px;
              background-color: #edfcff;
              border: 1px solid #4eaac6;
              line-height: 4.8rem;
              border-radius: 2.4rem;
              position: relative;
              figure.pic {
                position: absolute;
                top: 50%;
                right: calc(100% - 120px); }
              .txt {
                font-size: 1.8rem;
                font-weight: bold;
                color: #0f8fb6; }
              &:nth-child(1) {
                figure.pic {
                  width: 145px;
                  transform: translate(-13%,-40%); } }
              &:nth-child(2) {
                figure.pic {
                  width: 105px;
                  transform: translate(-15%,-53%); } }
              &:nth-child(3) {
                figure.pic {
                  width: 125px;
                  transform: translate(-13%,-71%); } } } } } } } } }
